import produce from 'immer';
import uuid from 'uuid';
import { FILE_UPLOADER_ACTION_ADD } from '../shared/Consts';
import {
  SURVEYJS,
  ELEMENTS_TYPE,
  SURVEYJS_MODE,
  SURVEYJS_STATUS,
} from '../components/FormBuilder/Constants';
import _get from 'lodash/get';
import _isEmpty from 'lodash/isEmpty';
import _has from 'lodash/has';
import _forEach from 'lodash/forEach';
import _invert from 'lodash/invert';

export const isFormInDisplayMode = mode => mode === SURVEYJS_MODE.display;

export const exportDataByTemplateElements = templateElements => {
  if (templateElements) {
    return templateElements.reduce((accumulator, item) => {
      const { name, questionId } = item;
      return {
        ...accumulator,
        [name]: questionId,
      };
    }, {});
  }
  return {};
};
export const exportDataByElements = elements => {
  if (elements) {
    return elements.reduce((accumulator, item) => {
      const questionName = _get(item, 'name');
      const questionId = _get(item, 'questionId');
      const templateElements = _get(item, 'templateElements');
      const elements = _get(item, 'elements');
      const nameAndQuestionIdInTemplate = exportDataByTemplateElements(
        templateElements,
      );
      const nameAndQuestionIdInElement = exportDataByElements(elements);
      return {
        ...accumulator,
        ...nameAndQuestionIdInTemplate,
        ...nameAndQuestionIdInElement,
        [questionName]: questionId,
      };
    }, {});
  }
  return {};
};
export const exportNameAndQuestionId = template => {
  const pages = _get(template, 'pages');
  return pages.reduce((accumulator, page) => {
    const elements = _get(page, ['elements']);
    const data = exportDataByElements(elements);
    return {
      ...accumulator,
      ...data,
    };
  }, {});
};

export const deepMapKeys = (obj, fn) =>
  Array.isArray(obj)
    ? obj.map(val => deepMapKeys(val, fn))
    : typeof obj === 'object'
    ? Object.keys(obj).reduce((acc, current) => {
        const key = fn(current);
        const val = obj[current];
        acc[key] =
          val !== null && typeof val === 'object' ? deepMapKeys(val, fn) : val;
        return acc;
      }, {})
    : obj;

export const getFormMode = status =>
  status === SURVEYJS_STATUS.submitted
    ? SURVEYJS_MODE.display
    : SURVEYJS_MODE.edit;

export const isFormSubmitted = formData => {
  if (!_has(formData, 'DataStatus')) return false;
  const status = _get(formData, 'DataStatus');
  return status === SURVEYJS_STATUS.submitted;
};

export const switchNameToId = (survey, template) => {
  let nameAndIdInData = {};
  let nameAndIdInTemplate = {};
  const data = _get(survey, ['data'], {});
  if (data) {
    const questions = exportNameAndQuestionId(template);
    nameAndIdInTemplate = deepMapKeys(
      data,
      name =>
        _get(survey.getQuestionByName(name), 'questionId', '') ||
        _get(questions, name, name),
    );
  }
  if (_has(survey, ['questionHashes', 'names'])) {
    const questionsName = Object.keys(
      _get(survey, ['questionHashes', 'names'], {}),
    );
    nameAndIdInData = questionsName.reduce((accumulator, name) => {
      const questionId = _get(survey.getQuestionByName(name), 'questionId', '');
      const questionValue = survey.getValue(name);
      return {
        ...accumulator,
        [questionId]: questionValue ?? '',
      };
    }, {});
  }
  return { ...nameAndIdInData, ...nameAndIdInTemplate };
};

export const applyFormProperty = (question, formProperty) => {
  if (_isEmpty(formProperty)) return;
  const questionId = _get(question, 'questionId');
  if (!formProperty[questionId]) return;
  const properties = Object.entries(formProperty[questionId]);
  properties.forEach(property => {
    const [propertyName, propertyValue] = property;
    if (question[propertyName])
      try {
        question[propertyName] = propertyValue;
      } catch (e) {
        //pass this propery
        //avoid improper properties
      }
  });
};

const fileQuestionDataParser = (questionsData, fileQuestions) => {
  if (_isEmpty(questionsData)) return {};
  if (_isEmpty(fileQuestions)) return questionsData;
  return produce(questionsData, draft => {
    const arr = Object.entries(draft);
    return arr.reduce((acc, curr) => {
      const [key, value = ''] = curr;
      if (fileQuestions[key] === ELEMENTS_TYPE.file)
        return { ...acc, [key]: fileDataParser(questionsData[key]) };
      return { ...acc, [key]: value };
    }, {});
  });
};

export const switchIdToName = (survey, data, fileQuestions, template) => {
  let nameAndIdInTemplate = {};
  let nameAndIdInData = {};
  if (data && template) {
    const questions = _invert(exportNameAndQuestionId(template));
    nameAndIdInTemplate = deepMapKeys(data, id => _get(questions, id, id));
  }
  if (_has(survey, ['questionHashes', 'names'])) {
    const questionsName = Object.keys(
      _get(survey, ['questionHashes', 'names'], {}),
    );
    nameAndIdInData = questionsName.reduce((accumulator, name) => {
      const questionId = _get(survey.getQuestionByName(name), 'questionId', '');
      return {
        ...accumulator,
        [name]: data[questionId],
      };
    }, {});
  }
  const allNameAndId = { ...nameAndIdInTemplate, ...nameAndIdInData };
  if (_isEmpty(fileQuestions)) return allNameAndId;
  /*
  the fileQuestions array contains questions that are file-typed
  the fileQuestions need base64 string to be the data while the data given by the API cannot match the format
  all file-typed questions data will be modified for the surveyjs to accept the data
  */
  return fileQuestionDataParser(allNameAndId, fileQuestions);
};

const fileDataParser = data => {
  if (_isEmpty(data)) return [];
  return data.reduce((acc, curr) => {
    const file = {
      name: _get(curr, 'name'),
      content: SURVEYJS.fileContent,
      file: _get(curr, 'file'),
      id: _get(curr, 'id'),
      key: _get(curr, 'key'),
      size: _get(curr, 'size'),
      type: _get(curr, 'type'),
    };
    return [...acc, file];
  }, []);
};

export const getFilesToUpload = files => {
  return produce(files, draft => {
    return draft.reduce((acc, curr) => {
      const id = uuid();
      const size = _get(curr, 'size');
      const name = _get(curr, 'name');
      curr.path = name;
      const newFile = {
        id,
        action: FILE_UPLOADER_ACTION_ADD,
        name,
        type: _get(curr, 'type'),
        size,
        file: curr,
        content: { id, size },
      };
      return [...acc, newFile];
    }, []);
  });
};

export const generateFileUpload = file => {
  const content = _get(file, 'content', {});
  const id = _get(content, 'id', {});
  const size = _get(content, 'size', {});
  if (!content || !id || !size) return {};
  return {
    id,
    action: FILE_UPLOADER_ACTION_ADD,
    name: _get(file, 'name'),
    file: { path: _get(file, 'name') },
    type: _get(file, 'type'),
    size,
  };
};

export const resetQuestionValue = options => (options.question.value = []);

const formatFileTypeQuestionData = data => {
  let fileQuestions = {};
  _forEach(data, function (multipleFiles, questionId) {
    _forEach(multipleFiles, function (fileContent) {
      const isContentFile = _get(fileContent, 'content', false);
      const isNameFile = _get(fileContent, 'name', false);
      const isType = _get(fileContent, 'type', false);
      const isFile = isContentFile && isNameFile && isType;
      const isOldFile = _get(fileContent, 'id', {});
      let fileContentFormat = {};
      if (_isEmpty(fileContent)) return;
      if (!isFile) return;
      if (_isEmpty(isOldFile)) {
        const newFile = generateFileUpload(fileContent);
        if (!_isEmpty(newFile)) {
          fileContentFormat = newFile;
        }
      } else {
        fileContentFormat = {
          file: _get(fileContent, 'file'),
          id: _get(fileContent, 'id'),
          key: _get(fileContent, 'key'),
          name: _get(fileContent, 'name'),
          size: _get(fileContent, 'size'),
          type: _get(fileContent, 'type'),
        };
      }

      if (fileQuestions[questionId]) {
        fileQuestions[questionId].push({ ...fileContentFormat });
      } else {
        fileQuestions[questionId] = [{ ...fileContentFormat }];
      }
    });
  });
  return fileQuestions;
};

export const formatSurveyData = (survey, fileTypeQuestions, formTemplate) => {
  const data = switchNameToId(survey, formTemplate);
  const fileQuestionData = formatFileTypeQuestionData(data);
  return {
    ...data,
    ...fileQuestionData,
  };
};

export const uploadFilesHandler = (file, urls, saveFormFiles) => {
  file.map(({ filesToUpload }) => {
    return filesToUpload
      .map(item => {
        const uploadUrls = urls.find(urlItem => urlItem.id === item.id);
        return {
          file: _get(item, 'file', {}),
          url: _get(uploadUrls, 'url', ''),
          options: {
            headers: {
              'Content-Type': _get(item, 'type'),
            },
          },
        };
      })
      .map(form => {
        return saveFormFiles(form);
      });
  });
};

export const onFilesUploadCallbackHandler = (options, getFormFiles) => {
  const { files, question } = options;
  const questionId = _get(question, 'questionId', '');
  const filesToUpload = getFilesToUpload(files);
  options.callback('success', filesToUpload);
  return getFormFiles({ questionId, filesToUpload });
};
